
.rec.rec-arrow {
  border-radius: 50;
  background: #333;
  color: #00ff00;
  display: none;
}
/* hide disabled buttons */
.rec.rec-arrow:disabled {
  visibility: hidden;
  background: #333;
  color: #00ff00;
   display: none;
}
.rec.rec-arrow::selection,
::after {
  background: #333;
  color: #00ff00;
   display: none;
}

.rec.rec-arrow::shadow {
  visibility: visible;
   display: none;
}
.rec.rec-dot {
  background: #C4C4C4;
  border: 2px #333;
  border-radius: 180;
}
.rec.rec-dot_active {
  border: #333;
  background:  #C4C4C4;
;
  
